  line-height: 1;
  letter-spacing: .02em;
  font-weight: 400;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-top: 16px;
  padding-right: 1rem;
  padding-left: 1rem;
}
.right{
	position: absolute;
	right: 0;
}



