
.editor{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.css-output{
   white-space: pre-wrap;
   position: relative;
   height: 40%;
   overflow-y: auto;
   border-top: 1px solid #000;
}
.inputs{
  height: 60%;
  overflow-y: auto;
}
.input{
  border-bottom: 1px solid #000;
  padding:0.5rem;
}
.input:last-child{
  border-bottom: none;
}
.input__button{
  display: block;
  padding: 0.5rem;
  margin-bottom: 0.5rem;
  border: 0;
  border-radius: 0.2rem;
  -webkit-box-shadow: 0 0.1rem 0.1rem 0 rgba(0,0,0,0.5);
          box-shadow: 0 0.1rem 0.1rem 0 rgba(0,0,0,0.5);
  color: #000;
  font-weight: bold;
  cursor: pointer;
}
.input__button--add{
  background-color: lightgreen;
  margin-top:0.25rem;
}
.input__button--remove{
  background-color: #F15A7E;
  display: inline;
  padding: 0.25rem;
}
.input__input--size{
  width: 3rem;
}
.button--clipboard{
  position: absolute;
  right: 0.5rem;
  top: 0.5rem;
}



