
.grid-area {
    z-index: 1;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: relative;
}
.grid-area.dragging,
.grid-area.resizing {
    opacity: 0.7;
}
.grid-area:not(.dragging):not(.resizing) {
    -webkit-transition: top ease-out 0.1s, left ease-out 0.1s, width ease-out 0.1s, height ease-out 0.1s;
    transition: top ease-out 0.1s, left ease-out 0.1s, width ease-out 0.1s, height ease-out 0.1s;
}
.grid-area .resize-handle {
    position: absolute;
    right: -5px;
    bottom: -5px;
    width: 15px;
    height: 15px;
    cursor: se-resize;
}
.remove-button {
  position: absolute;
  right: 0;
  top: 0;
  width: 15px;
  height: 15px;
  font-weight: bold;
  color: #f33;
  cursor: pointer;
}
.input--rename{
  width: 100%;
  min-width: 5rem;
  -webkit-box-sizing: border-box;
