
.about {
	height: calc(100vh - 56px);
	position: relative;
}
.about-content{
	height: 80%;
}
.footer{
	margin: 1rem 0;
	color: #333;
	font-size: 90%;
	text-align: center;
}
.footer a {
	color: #77c;
	text-decoration: none;
	-webkit-transition: color .25s linear;
	transition: color .25s linear;
}
.footer a:hover {
	color: #C9444D;
}
.div--margintop{
	margin-top: 1rem;
}
.padded{
	padding: 0 1rem;
}
@supports (grid-area: auto) {
.about{
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: auto 6rem;
    grid-row-gap: 1rem;
    grid-template-areas:
    "aboutcontent"
    "footer";
}
.about-content {
    grid-area: aboutcontent;
}
.footer {
    grid-area: footer;
}
}




